@media (max-width: 768px) {
    .container {
        padding: 0 4vw;
    }

    .button {
        padding: 3.8vw;
        border-radius: 5vw;
        height: 8.6vw;
        font-size: 3.5vw;
    }

    .whatsapp {
        height: 7.1vw;
        font-size: 3vw;
    }

    .title-block {
        padding-top: 10vw;

        h1 {
            margin-bottom: 4vw;
            font-size: 7.1vw;
            max-width: 100%;
            line-height: 1.16;
        }

        p {  
            margin-bottom: 1vw;
            max-width: 100%;
        }
    }

    .text {
        font-size: 4.1vw;
        line-height: 1.36;    
    }

    .feedback-block {
        &__services {
            margin-left: 3vw;
            font-size: 3.5vw;
        }
    }

    .key-word {
        font-size: 3vw;
        padding: 1.8vw 2.6vw;
    }

   
    .our-business {
        text-align: left;

        .second-title {
            min-width: 100%;
        }

        &__big-cards, &__small-cards {
            grid-template-columns: 1fr;
        }

        &__big-card {
            height: 68vw;
            padding: 6.2vw;
        }

        &__small-cards {
            grid-auto-rows: 45.2vw;
        }

        &__small-card {
            padding: 6vw;

            i {
                font-size: 10vw;
                
            }

            &:nth-child(1) {
                .text {
                    width: 100%;
                }
            }

            &:nth-child(2) {
                .text {
                    width: 90%;
                }
            }

            &:nth-child(4) {
                .text {
                    width: 100%;
                }
            }

            &:nth-child(5) {
                .text {
                    width: 100%;
                }
            }

            &:nth-child(6) {
                .text {
                    width: 90%;
                }
            }
        }

        .text {
            margin-bottom: 2.5vw
        }

        &__small-text {
            font-size: 3.34vw;
        }

    }

    .feedback {
        &__wrapper {
            flex-direction: column;
        }

        .second-title, .form {
            width: 100%;
        }
    }

    .geo {
        &__wrapper {
            flex-direction: column;
        }

        &__map {
            width: 100%;
            height: auto;
        }
    }

    .stages {
        flex-direction: column;
    }

    .second-title {
        font-size: 5.4vw;
        line-height: 1.22;
    }

    
}